
export class StorageService {

    getCheclists() {
        var list = [];
        for (var p in localStorage) {
            if (localStorage.getItem(p)!=null) {
                var item = JSON.parse(localStorage.getItem(p));
                list.push({
                    id: p,
                    name: item.name,
                    items: item.items
                });
            }
        }
        return list;
    }

    getChecklist(id) {
        var itemStr = localStorage.getItem(id);
        var item=itemStr!=null?JSON.parse(itemStr):{name:"",items:[]};
        item.items = item.items.filter(x => x.length>0);
        return item;
    }

    store(id, item) {
        localStorage[id]=JSON.stringify(item);
    }

}