import React from 'react';
import {Component} from "react";
import Checkbox from '@material-ui/core/Checkbox';
import {Link} from "react-router-dom";
import Button from "@material-ui/core/Button";
import {StorageService} from "./StorageService";


export class Checklist extends Component {

    constructor(props) {
        super(props);
        console.log(props);
        this.state = {
                name : props.name,
                text: props.items.join("\n")
            };
        console.log("constructor----");
        console.log(this.state);
        console.log("constructor----");
        this.save = this.save.bind(this);
        this.onNewLetterName = this.onNewLetterName.bind(this);
        this.onNewLetterText = this.onNewLetterText.bind(this);
    }

    componentDidMount() {
        document.title=this.props.name;
    }

    onNewLetterName(event) {
        this.setState({name: event.target.value});
    }

    onNewLetterText(event) {
        this.setState({text: event.target.value});
    }

    save() {
        var element = document.getElementById("checklistContent");
        var text = element.value;
        var id = this.props.itemId;
        var name = document.getElementById("checklistName").value;
        if (id=="newOneMarker") {
            id = new Date()*1;
        }
        new StorageService().store(id, {name:name, items:text.split("\n")});
        window.location="#/";
    }

    render() {
        var elems = [];
        var id = Math.random()+new Date()
        var items = this.props.items||[];
        var name = this.props.name || "";
        let back = <div><br /><Link to="/list">&lt;&lt; Back</Link></div>;
        // var text = encodeURIComponent(JSON.stringify(items));
        if (this.props.edit) {
            return <div>Name:<input id="checklistName" type="text" value={this.state.name} style={{fontSize:"2em"}} onChange={this.onNewLetterName} /><br />
                <textarea id="checklistContent" checklistId={id} rows={20} cols={50} style={{fontSize:"1em"}} value={this.state.text} onChange={this.onNewLetterText}></textarea><br /><Button onClick={this.save}>Save</Button>{back}</div>
        } else {
            for (var i = 0; i < items.length; i++) {
                var label = items[i];
                if (this.props.edit) {
                    label = <input type="text" value={label}></input>;
                } else {
                    label = <label for={id + "item_" + i}>{label}</label>
                }
                elems.push(<div><Checkbox id={id + "item_" + i} label={"item " + i}></Checkbox>{label}</div>)
            }
            return <div><h1>{this.props.name}</h1>{elems}{back}</div>;
        }
    }
}