import React from 'react';
import {Component} from "react";
import {StorageService} from "./StorageService";
import {Link} from "react-router-dom";
import Fab from '@material-ui/core/Fab';


export class List extends Component {
    componentDidMount() {
        document.title="Checklists...";
    }

    add() {
        var newId = new Date()*1;
        document.location="/#edit/"+newId;
    }

    delete(element) {
        var idToDelete = element.target.getAttribute("item");
        if (window.confirm("Delete?")) {
            localStorage.removeItem(idToDelete);
        }
    }

    render() {
        const style = {
            margin: 0,
            top: 'auto',
            right: 20,
            bottom: 20,
            left: 'auto',
            position: 'fixed',
        };
        var list = new StorageService().getCheclists();
        var elems = [];
        for (var i=0; i<list.length; i++) {
            elems.push(<div style={{widht:"100%",padding:"10pt",fontSize:"1.5em"}}><Link to={"/view/"+list[i].id}>{list[i].name}</Link><span style={{float:"right"}}><Link item={list[i].id} onClick={this.delete}>&#x1F5D1;</Link>&nbsp;&nbsp;<Link to={"/edit/"+list[i].id}>&#x270E;</Link></span></div>);
        }
        return <div id="list">Available checklists:<br />{elems}<Fab style={style} onClick={this.add} color="primary" aria-label="add">+</Fab></div>;
    }
}